@import "src/styles/variables.scss";
@import "src/styles/mixin.scss";

.gallery {
    @include media(mobile-only) {
        width: 100%;
        margin-top: 32px;
        gap: 0;
    }

    margin-top: 61px;
    margin-bottom: 188px;
    display: flex;
    flex-wrap: wrap;
    gap: 24px 20px;
    max-width: 1020px;
}

.gallery__item {
    @include media(mobile-only) {
        width: 100%;
        margin-bottom: 32px;
    }

    width: 240px;
    background-color: "#fff";
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.2),
    0px 3px 4px rgba(0, 0, 0, 0.12),
    0px 2px 4px rgba(0, 0, 0, 0.14);
    border-radius: 4px;
    cursor: pointer;
}

.gallery__item:hover {
    transform: scale(1.03);
    cursor: zoom-in;
}

.gallery__link {
    text-decoration: none;
}

.gallery__image {
    @include media(mobile-only) {
        height: 232px;
    }

    width: 100%;
    height: 188px;
    border-top-left-radius: 4px;
    border-top-right-radius: 4px;
}

.gallery__info {
    padding: 12px 16px;
}

.gallery__name {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 30px;
    letter-spacing: 0.15px;
    color: rgba(0, 0, 0, 0.9);
}

.gallery__species {
    font-family: "Roboto";
    font-style: normal;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    letter-spacing: 0.25px;

    color: rgba(0, 0, 0, 0.6);
}